.scale {
    transform: matrix(1, 0, 0, 1, 0, 0);
}
.scale:hover {
    animation: move .3s ;
}
@keyframes move {
    0% {
        transform: matrix(1, 0, 0, 1, 0, 0);
    }
    50% {
        transform: matrix(1, 0, 0, 1, 0, -5);
    }
    100% {
        transform: matrix(1, 0, 0, 1, 0, 0);
    }
}
